<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>我的服务</p>
        </section>
        <section class="middle">
            <div class="md-example-child md-example-child-tabs md-example-child-tab-bar-6 top-tab-bar">
                <div class="none" v-if="!listLeft.length"  :style="{height: Height}">暂无数据</div>
                <md-scroll-view v-if="listLeft.length" :style="{height: Height}" @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in listLeft"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.img" alt="">
                        <div class="article">
                            <p v-html="i.name"></p>
                            <span>{{i.manufacturer}}<span class="button-def" inline>服务详情</span></span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/personal/server.js'
import { TabBar, Swiper, Icon, Button, SwiperItem, ScrollView, ScrollViewMore, Toast } from 'mand-mobile'
export default {
  name: 'News',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [Button.name]: Button,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [Icon.name]: Icon,
    [ScrollViewMore.name]: ScrollViewMore,
    [Toast.name]: Toast
  },
  data () {
    return {
      // 当前选中tab
      current: 9,
      // tab项
      items: [{ name: 9, label: '设备共享' }],
      listLeft: [],
      isFinished: false,
      Height: '900px',
      listRight: [],
      searchParams: {
        status: 1,
        pageSize: 10,
        pageNumber: 1
      }
    }
  },
  mounted () {
    this.getList()
    this.Height = window.innerHeight + 'px'
  },
  methods: {
    getList () {
      getList(this.searchParams).then(res => {
        this.listLeft = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    onTabChange (item, index) {
      this.searchParams.pageNumber = 1
      this.searchParams.typeId = item.name
      this.getList()
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/personal/serverDetail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.listLeft = this.listLeft.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.push('/personal')
    }
  }
}
</script>

<style lang="stylus" scoped>
    .middle
        position fixed
        top 88px
        width 100%
    /deep/.md-tab-bar-list .is-active
        color #fff!important
        background #021955!important
    /deep/.md-tab-bar-item
        background #fff!important
        color #999999!important
    .md-example-child-tab-bar-6 .md-swiper
        background #FFF
        .md-swiper-item
            padding 20px
            box-sizing border-box
            font-size 24px
            line-height 1.5
            color #666
    .md-example-child-scroll-view-0
        height 600px
        background #FFF
        .scroll-view-item
            margin-bottom 30px
            text-align center
            font-size 28px
            font-family DINAlternate-Bold
            height 174px
    .scroll-view-container
        .scroll-view-item
            margin-bottom  50px
            padding-bottom 20px
            border-bottom 2px solid rgba(153,153,153,.5)
            display flex
            justify-content space-between
            img
                width 260px
                height 152px
            .article
                width 410px
                text-align left
                margin 10px 0
                display flex
                flex-direction column
                justify-content space-between
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display flex
                    align-items center
                    line-height 24px
                    justify-content: space-between
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
        .md-scroll-view-more
            justify-content center
    /deep/.md-tab-bar .md-tab-bar-list
        border-top 0!important
    .md-scroll-view
        padding 20px
    /deep/.md-button
        float left
        width:160px;
        height:50px;
        color #fff
        background:rgba(2,25,85,1);
        border-radius:8px;
    /deep/.default:after
        border none
</style>
